/***/ (function(module, exports) {

	module.exports = {"data":{"site":{"siteMetadata":{"title":"Crazy Woman","subtitle":"Enlouqueça para encontrar a lucidez.","copyright":"© Todos os direitos reservados","menu":[{"label":"Artigos","path":"/"},{"label":"Sobre","path":"/sobre/"},{"label":"Contato","path":"/contato/"}],"author":{"name":"Mariana Brito","facebook":"https://www.facebook.com/briitomari","twitter":"https://twitter.com/briitomari","instagram":"https://www.instagram.com/briitomari/","github":"https://github.com/BritoMari","rss":"#","email":"mariana.bsouza@hotmail.com"}}},"markdownRemark":{"id":"/Users/marianabrito/dev/crazywoman/src/pages/articles/2017-12-22---Somos-ensinados-a-odiar/index.md absPath of file >>> MarkdownRemark","html":"<p>Não desista de praticar o bem só porque algo mal te aconteceu, dizem que todos nascemos com aquele instinto de fazer o bem e nada mais, dizem também que ninguém é ensinado a fazer o mal, eu particularmente discordo dessa última afirmação.</p>\n<p>Basicamente todos os dias eu sofro preconceito por ser quem eu sou, <strong>por andar de mãos dadas com a pessoa que amo e divido a vida, sou criticada por amar e por fazer o bem</strong>, na verdade me olham como uma pessoa que só quer desgraça, enquanto o que eu quero é simplesmente acordar num dia nublado com um mundo livre de ódio e destruição.</p>\n<p>Eu acho sim, que <strong>somos ensinados a odiar, a fazer o mal e olhar diferente para as pessoas</strong>. Somos quando pais, amigos, conhecidos e a mídia nos ensinam que aquele ser humano que ama outra <a href=\"https://observatoriog.bol.uol.com.br/noticias/2017/04/casal-gay-de-60-e-62-anos-e-espancado-por-mais-de-20-vizinhos-no-rio-de-janeiro\">pessoa do mesmo sexo</a> é ruim, ou que somos melhores do que aquele <a href=\"https://g1.globo.com/pr/parana/noticia/morador-de-rua-e-encontrado-morto-em-rua-do-bacacheri-em-curitiba.ghtml\">morador de rua</a>, que aquele usuário de drogas não merece uma condição melhor de vida e por aí vai a longa lista de ensinamentos de ódio e sentimento de inferioridade sobre os outros.</p>\n<p>As vezes nos perguntamos porque há tanto rancor e desamor no mundo, sendo que nós <strong>praticamos isso todos os dias, sem nem perceber</strong>, como por exemplo quando negamos R$2,00 aquela moça que está com sua filha na porta de um Shopping, com seu carrinho que pega reciclados na rua. Inclusive ontem eu me vi fazendo isso, eu quase passei direto, só que o olhar daquela criança me doeu, eu voltei atrás e dei todo o dinheiro que havia no meu bolso pra aquela mãe, não era muita coisa, mas sei que ajudou, sei que fiz a diferença.</p>\n<p>Precisamos de um choque de realidade pra acordar, fazer a diferença e ajudar o mundo a se tornar melhor do que foi ontem.</p>\n<p><img src=\"https://raw.githubusercontent.com/BritoMari/crazywoman/master/src/pages/articles/2017-12-22---Somos-ensinados-a-odiar/preserve.jpg\" alt=\"Preserve! Pessoas boas estão sendo extintas\"></p>\n<p>Nos perguntamos porque pessoas boas morrem, infelizmente <strong>ela não morre por ser boa</strong>, mas sim porque é o ciclo da vida e nada pode impedir, ela já completou a sua trajetória. </p>\n<p>E sobre aqueles que estão no caminho do mal, eu tenho esperança <strong>que encontrem o caminho do bem</strong>, pra tentar pelo menos se recompensar por todo mal que fez.</p>\n<p>Faça o bem mesmo que o mundo vá mal, mesmo que te critiquem e te atirem pedras pelo caminho, não desista porque alguém não soube amar e muito menos desista desse mundo cruel.</p>","fields":{"tagSlugs":["/tags/amor/","/tags/odio/","/tags/mundo/"]},"frontmatter":{"title":"Somos ensinados a odiar?","tags":["Amor","Ódio","Mundo"],"date":"2017-12-22T12:58:15.139Z","description":"Não desista de praticar o bem só porque algo mal te aconteceu, dizem que todos nascemos com aquele instinto de fazer o bem e nada..."}}},"pathContext":{"slug":"/posts/somos-ensinados-a-odiar/"}}

/***/ })

